       \"gray\": var(--gray200),\n        ),\n        hovered: (\n                \"green-hover\": var(--green600),\n                \"orange-hover\": var(--orange1),\n                \"red-hover\": var(--berry2),\n                \"primary-hover\": var(--blue2),\n                \"gray-hover\": var(--gray400),\n        )\n);\n\n\n@mixin setThemeBtn {\n  @each $color, $value in map-get($themeColours, base) {\n    $hoverColor: map-get(map-get($themeColours, hovered), #{$color + \"-hover\"});\n\n    &-#{$color} {\n      @include btnColor($value, $hoverColor);\n      &--outline {\n        @include btnOutline($value, $hoverColor);\n      }\n      &--transparent {\n        @include btnTransparent($value, $hoverColor);\n      }\n    }\n  }\n}\n\n@keyframes rotation {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.btn {\n  @include baseBtn;\n  position: relative;\n\n  &-fullWidth {\n    width: 100%;\n    max-width: 100%;\n  }\n  &-withIcon {\n    justify-content: space-between;\n    align-items: center;\n  }\n  &-small {\n   font-size: 0.9rem;\n  }\n\n  &-medium {\n    font-size: 1.1rem;\n    width: 230px;\n  }\n  &-disabled {\n    opacity: 0.5;\n    pointer-events: none;\n  }\n\n  &LoadingContainer {\n    @include round-loading-container\n  }\n  &LoadingLabel {\n    opacity: 0;\n  }\n  &-loading {\n    width: 20px;\n    height: 20px;\n    transform: translate(50%, -50%);\n    border: 2px solid #FFF;\n    border-bottom-color: transparent;\n    border-radius: 50%;\n    display: inline-block;\n    animation: rotation 1s linear infinite;\n  }\n\n  @include setThemeBtn;\n}\n","@import './_variable.scss';\n@mixin check-control-container {\n  display: block;\n  position: relative;\n  cursor: pointer;\n  font-size: 14px;\n  padding-left: 35px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n@mixin input-check-control {\n  position: absolute;\n  opacity: 0;\n  cursor: pointer;\n  height: 0;\n  width: 0;\n}\n\n@mixin input-checkmark {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 18px;\n  width: 18px;\n  background-color: var(--gray100);\n\n  &::after {\n    content: \"\";\n    position: absolute;\n    display: none;\n  }\n}\n\n@mixin input-checkmark-background {\n  left: 5px;\n  top: 2px;\n  width: 4px;\n  height: 8px;\n  border: solid white;\n  border-width: 0 3px 3px 0;\n  -webkit-transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n  transform: rotate(45deg);\n}\n\n@mixin baseBtn {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  min-width: 150px;\n  width: 100%;\n  min-height: 36px;\n  padding:  0.375rem 0.75rem;\n  border-radius: 0.5rem;\n  border: none;\n  font-size: 1rem;\n  cursor: pointer;\n  color: #fff;\n  gap: 1rem;\n}\n@mixin btnColor($color, $hoverColor) {\n  color: #fff;\n  background: $color;\n\n  &:hover {\n    background: $hoverColor;\n  }\n\n  & > span, & > path {\n    color: #fff;\n    fill: #fff;\n  }\n}\n\n@mixin btnOutline($color, $hoverColor) {\n  background: transparent;\n  color: $color;\n  border: 2px solid $color;\n  &:hover {\n    color: $hoverColor;\n    border: 2px solid $hoverColor;\n    background: transparent;\n  }\n  & > span, & > path {\n    color: $color;\n    fill: $color;\n  }\n}\n\n@mixin btnTransparent($color, $hoverColor) {\n  background: transparent;\n  border: none;\n  color: $color;\n  fill: $color;\n  &:hover {\n    color: $hoverColor;\n    background: transparent;\n  }\n}\n\n@mixin round-loading-container {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 10;\n  height: 100%;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n"],"sourceRoot":""}]);
